import "./App.css";
import Header from "./components/Header";
import Tasks from "./components/Tasks";
import AddTasks from "./components/AddTasks";
import { useState } from "react";

function App() {
  const appStyle = {
    display: "flex",
    margin: "0 auto",
    minWidth: "300px",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    padding: "1.5rem 1.5rem",
    borderRadius: "10px",
    boxShadow: "0px 0px 15px 10px rgba(137,137,137,0.7)",
  };

  const [taskName, setTaskName] = useState("");
  const [tasks, setTasks] = useState([
    {
      id: 1,
      name: "Water plants",
      isCompleted: true,
    },
    {
      id: 2,
      name: "Cook lunch",
      isCompleted: true,
    },
    {
      id: 3,
      name: "Go to the gym",
      isCompleted: false,
    },
  ]);

  // functions
  const deleteTask = id => {
    setTasks(
      tasks.filter(task => {
        return task.id !== id;
      })
    );
  };

  const completedTask = id => {
    setTasks(
      tasks.map(task => {
        return task.id === id
          ? { ...task, isCompleted: !task.isCompleted }
          : task;
      })
    );
  };

  const addTask = e => {
    e.preventDefault();
    if (taskName === null || taskName === "") {
      return alert("Please enter task name");
    }
    setTasks(
      tasks.concat({ name: taskName, id: Date.now(), isCompleted: false })
    );
    setTaskName("");
  };

  return (
    <div className="App" style={appStyle}>
      <Header />
      <AddTasks
        taskName={taskName}
        setTaskName={setTaskName}
        addTask={addTask}
      />
      <Tasks
        tasks={tasks}
        deleteTask={deleteTask}
        completedTask={completedTask}
      />
    </div>
  );
}

export default App;
